body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



main {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 1100px;
  margin: 0 auto;
}

.useCaseNarative .row {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  align-items: flex-start;
}

input,
textarea {
  flex-grow: 1;
  resize: none;

  min-height: 30px;
  font-size: 18px;

  border: 1px solid #ccc;
  padding: 1px;
  font-family: "Calibri", sans-serif;
}

.row>p {
  display: flex;
  flex-direction: row;
  width: 100%;
}

span {
  width: max-content;
  text-wrap: nowrap;
}

span.sameWidth {
  width: 130px;
}

.W10 {
  flex: 1;
}

.W20 {
  flex: 2;
}

.W30 {
  flex: 3;
}

.W40 {
  flex: 4;
}

.W80 {
  flex: 8;
}

.W50 {
  flex: 5;
}

.W60 {
  flex: 6;
}

.useCaseNarative>section {
  margin-top: 15px;
}

.flowgrid input {
  font-size: 24px;
  min-height: 40px;

}

.heading {
  font-size: 20px;
  font-weight: bold;
}

.flowgrid {
  display: grid;
  grid-template-columns: 100px 1fr 1fr;
}




.flowgrid input,
.flowgrid textarea {
  width: 100%;
}

span {
  margin: 0 5px;
}

h2 {
  margin-left: 5px;
}


button.add {
  width: 64px;
  aspect-ratio: 1;
  border-radius: 50%;
  background-color: #4CAF50;
  color: white;
  font-size: 48px;
  border: none;
  cursor: pointer;

  transition: background-color 0.1s, transform 0.1s;
  margin: 30px auto;
}

button.add:hover {
  background-color: #45a049;
  transform: scale(1.05);
}

.quickAcitons {
  position: absolute;
  left: -70px;
  top: 60px;

  display: flex;
  flex-direction: column;
  gap: 10px;
}

.quickAcitons button {
  width: 50px;
  aspect-ratio: 1;
  font-size: 24px;
  border: none;
  cursor: pointer;
  border-radius: 50%;

  display: flex;
  align-items: center;
  justify-content: center;
}

button.move {
  background-color: #2196F3;
  color: white;
}

button.delete {
  background-color: #f44336;
  color: white;
}

li {
  position: relative;

  list-style: none;
}

.useCaseNarative {
  margin-top: 50px;

  padding: 30px;
  border-radius: 20px;

  background-color: #f9f9f9;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

img {
  width: 40px;
  height: 100%;
}



#loading {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  display: flex;
  align-items: center;
  justify-content: center;

  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1000;
  display: flex;
}

.loaderWrap {
  width: 100%;
  height: 100%;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

#loading .loader {
  width: 100px;
  height: 100px;
  border: 6px solid #f3f3f3;
  border-top: 6px solid #3498db;
  border-radius: 50%;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

div.tools {
  position: absolute;
  top: 5px;
  left: 5px;

  display: flex;
  flex-direction: column;

  gap: 20px;
  width: 250px;
}

div.tools select,
div.tools button {
  font-size: 18px;
}

div.tools select {
  padding: 5px;
}

div.tools section {
  display: flex;
  flex-direction: column;
  gap: 5px;
}